import { getData } from './authReducer'
import { Dispatch } from 'react'
import { ThunkAction } from 'redux-thunk'
import { AppStateType } from './redux-store'

const INITIALIZING_SUCCESS = 'INITIALIZING_SUCCESS'

export type InitialStateType = {
    initialized: boolean
}

let initialState: InitialStateType = {
    initialized: false
}

const appReducer = (state = initialState, action: ActionTypes): InitialStateType => {
    switch(action.type) {
        case INITIALIZING_SUCCESS:
            return {
                ...state,
                initialized: true,
            }
        
        default: 
            return state
    }
}

type InitializedSuccessActionType = {
    type: typeof INITIALIZING_SUCCESS
}

type DispatchType = Dispatch<ActionTypes>
type ActionTypes = InitializedSuccessActionType
type ThunkType = ThunkAction<Promise<void>, AppStateType, unknown, ActionTypes>

export const initializedSuccess = (): InitializedSuccessActionType => ({type: INITIALIZING_SUCCESS})

export const initializeApp = () => (dispatch: any) => {
    let promise = dispatch(getData())
    Promise.all([promise])
    .then(() => {
        dispatch(initializedSuccess())
    })
}

export default appReducer
