import {profileAPI} from '../api/api'
import { stopSubmit } from 'redux-form'
import { PhotosType } from '../types/types'
import { Dispatch } from 'react'
import { ThunkAction } from 'redux-thunk'
import { AppStateType } from './redux-store'

const ADD_POST = 'ADD-POST'
const SET_USERS_PROFILE = 'SET-USERS-PROFILE'
const SET_STATUS = 'SET_STATUS'
const DELETE_POST = 'DELETE_POST'
const SAVE_PHOTO_SUCCESS = 'SAVE_PHOTO_SUCCESS'

type PostsType = {
  id: number,
  post: string
}

export type ContactsType = {
  github: string,
  vk: string,
  facebook: string,
  instagram: string,
  twitter: string,
  youtube: string,
  website: string,
  mainLink: string
}

export type ProfileType = {
  userId: number,
  lookingForAJob: boolean,
  lookingForAJobDescription: string,
  fullName: string,
  contacts: ContactsType,
  photos: PhotosType
}

let initialState = {
        postData: [
            {id: 1, post: 'PET_PROJECT'},
            {id: 2, post: 'REACT-REDUX'},
            {id: 3, post: 'POST'},
            {id: 4, post: 'POST'},
          ] as Array <PostsType>,
        profile: null as ProfileType | null,
        status: ''
}

export type InitialStateType = typeof initialState

type DispatchType = Dispatch<ActionTypes>
type ActionTypes = OnAddPostType | SavePhotoSuccessType | SetUsersProfile | SetStatusType | DeletePostType
type ThunkType = ThunkAction<Promise<void>, AppStateType, unknown, ActionTypes>
type GetStateType = AppStateType

const profileReducer = (state = initialState, action: ActionTypes): InitialStateType => {
    switch(action.type) {
      case ADD_POST: {
        let newPost = {
            id: 9, post: action.newPostText
        }
        return {
            ...state, 
            postData: [...state.postData, newPost],
        }
    }
        case SET_USERS_PROFILE:
          return {...state, profile: action.profile}

        case SET_STATUS:
          return {...state, status: action.status}

        case DELETE_POST:
          return {...state, postData: state.postData.filter(p => p.id !== action.id)}

        case SAVE_PHOTO_SUCCESS:
          return {...state, profile: {...state.profile, photos: action.photos} as ProfileType}

        default:
            return state
    }
}

export const getProfile = (userId: number): ThunkType => async (dispatch: any) => {
    let data = await profileAPI.getProfile(userId)
      dispatch(setUsersProfile(data))
}

export const getStatus = (userId: number): ThunkType => async (dispatch) => {
    let data = await profileAPI.getStatus(userId)
      dispatch(setStatus(data))
  }

export const updateStatus = (status: string): ThunkType => async (dispatch) => {
    let data = await profileAPI.updateStatus(status)
      if(data.resultCode === 0) {
        dispatch(setStatus(status))
      }
  }

  export const savePhoto = (file: any): ThunkType => async (dispatch) => {
    let data = await profileAPI.savePhoto(file)
      if(data.resultCode === 0) {
        dispatch(savePhotoSuccess(data.data.photos))
      }
  }

  export const saveProfile = (profile: ProfileType): ThunkType => async (dispatch: any, getState: any) => {
    const userId = getState().auth.userId
    let data = await profileAPI.saveProfile(profile)
      if(data.resultCode === 0) {
        dispatch(getProfile(userId))
      } else {
        dispatch(stopSubmit("edit-profile", {_error: data.messages[0]}))
      }
  }

type OnAddPostType = {
  type: typeof ADD_POST,
  newPostText: string
}
  
export const onAddPost = (newPostText: string): OnAddPostType => ({type: ADD_POST, newPostText})

type SavePhotoSuccessType = {
  type: typeof SAVE_PHOTO_SUCCESS,
  photos: PhotosType
}
export const savePhotoSuccess = (photos: PhotosType): SavePhotoSuccessType => ({type: SAVE_PHOTO_SUCCESS, photos})

type SetUsersProfile = {
  type: typeof SET_USERS_PROFILE,
  profile: ProfileType
}
export const setUsersProfile = (profile: ProfileType): SetUsersProfile => ({type: SET_USERS_PROFILE, profile})

type SetStatusType = {
  type: typeof SET_STATUS,
  status: string
}
export const setStatus = (status: string): SetStatusType => ({type: SET_STATUS, status})

type DeletePostType = {
  type: typeof DELETE_POST,
  id: number
}
export const deletePost = (id: number): DeletePostType => ({type: DELETE_POST, id})
export default profileReducer
