const ADD_MESSAGE = 'ADD-MESSAGE'

type DialogType = {
    id: number,
    name: string
}

type MessageType = {
    id: number,
    message: string
}

let initialState = {
    dialogsData: [
        {id: 1, name: 'User 1'},
        {id: 2, name: 'User 2'},
        {id: 3, name: 'User 3'},
        {id: 4, name: 'User 4'},
        {id: 5, name: 'User 5'},
    ] as Array<DialogType>,
    
    messagesData: [
      {id: 1, message: 'Fight Club'},
      {id: 2, message: 'Shutter 2004'},
      {id: 3, message: 'GTO'},
      {id: 4, message: 'Rasen 1998'},
      {id: 5, message: 'Black Lagoon'},
    ] as Array<MessageType>,
}

export type InitialStateType = typeof initialState

const dialogsReducer = (state = initialState, action: any): InitialStateType => {
    switch (action.type) {
        case ADD_MESSAGE:
            return {
                ...state,
                messagesData: [...state.messagesData, {id: 9, message: action.newMessageBody}]
            }

        default: 
            return state
    }
}

type NewMessageBodyType = {
    type: typeof ADD_MESSAGE,
    newMessageBody: string
}

export const sendMessage = (newMessageBody: string): NewMessageBodyType => ({type: ADD_MESSAGE, newMessageBody})

export default dialogsReducer