import { ContactsType } from './../redux/profileReducer';
import axios from 'axios'
import { ProfileType } from '../redux/profileReducer'
import { UserType, PhotosType } from '../types/types'

const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {"API-KEY": "3db790bc-68c8-44d6-839e-037f7aa28043"}
})

type UpdateStatusResponseType = {
    resultCode: ResultCodesEnum,
    messages: Array<string>,
    data: {}
}

type GetProfileResponseType = {
    userId: number,
    lookingForAJob: boolean,
    lookingForAJobDescription: string,
    fullName: string,
    contacts: ContactsType,
    photos: PhotosType
    
}

export const profileAPI = {
    getProfile(userId: number) {
        return instance.get<GetProfileResponseType>(`profile/` + userId)
        .then(response => {
            return response.data
        })
    },

    getStatus(userId: number) {
        return instance.get(`profile/status/` + userId)
        .then(response => {
            return response.data
        })
    },

    updateStatus(status: string) {
        return instance.put<UpdateStatusResponseType>(`profile/status/`, {status: status})
        .then(response => {
            return response.data
        })
    },

    savePhoto(photoFile: any) {
        let formData = new FormData()
        formData.append('image', photoFile)
        return instance.put(`profile/photo/`, formData, {headers: {'Content-Type': 'multipart/form/data'}})
        .then(response => {
            return response.data
        })
    },

    saveProfile(profile: ProfileType) {
        return instance.put(`profile`, profile)
        .then(response => {
            return response.data
        })
    }
}

type UsersResponseType = {
    items: Array<UserType>,
    totalCount: number,
    error: string
}

export const usersAPI = {
    getUsers (currentPage: number, pageSize: number) {
        return instance.get<UsersResponseType>(`users?page=${currentPage}&count=${pageSize}`)
        .then(response => {
            return response.data
        })
    },
    
    follow (userId: number) {
        return instance.post(`follow/${userId}`)
        .then(response => {
            return response.data
        })
    },
    
    unfollow (userId: number) {
        return instance.delete(`follow/${userId}`)
        .then(response => {
            return response.data
        })
    },
}

export enum ResultCodesEnum {
    Success = 0,
    Error = 1,
}

export enum ResultCodeForCaptchEnum {
    CaptchaIsRequired = 10
}

type MeResponseType = {
    data: {id: number, email: string, login: string},
    resultCode: ResultCodesEnum,
    messages: Array<string>,
}

type LoginResponseType = {
    data: {userId: number},
    resultCode: ResultCodeForCaptchEnum | ResultCodesEnum,
    messages: Array<string>,
}

export const authAPI = {
    me() {
        return instance.get<MeResponseType>(`auth/me`)
        .then(response => {
            return response.data
        })
    },

    login(email: string, password: string, rememberMe = false, captcha: string | null = null) {
        return instance.post<LoginResponseType>(`auth/login`, {email, password, rememberMe, captcha})
        .then(response => {
            return response.data
        })
    },

    logout() {
        return instance.delete(`auth/login`)
        .then(response => {
            return response.data
        })
    }
}

export const securityAPI = {
    getCaptchaUrl() {
        return instance.get(`security/get-captcha-url`)
        .then(response => {
            return response.data
        })
    },
}

