import {authAPI, securityAPI, ResultCodeForCaptchEnum, ResultCodesEnum} from '../api/api'
import {stopSubmit} from 'redux-form'
import { Dispatch } from 'react'
import { ThunkAction } from 'redux-thunk'
import { AppStateType } from './redux-store'

const SET_USER_DATA = 'SET-USER-DATA'
const GET_CAPTCHA_URL_SUCCESS = 'GET_CAPTCHA_URL_SUCCESS'

let initialState = {
    id: null as number | null,
    email: null as string | null,
    login: null as string | null,
    isAuth: false,
    captchaUrl: null as string | null
}

export type InitialStateType = typeof initialState

const authReducer = (state = initialState, action: ActionTypes): InitialStateType => {
    switch(action.type) { 
        case SET_USER_DATA:
        case GET_CAPTCHA_URL_SUCCESS:
            return {
                ...state,
                ...action.payload,
            }

        default: 
            return state
    }
}

type ActionTypes =  SetAuthUserDataType | GetCaptchaUrlSuccessType

type SetAuthUserDataPayloadType = {
    id: number | null
    email: string | null
    login: string | null
    isAuth: boolean
}

type SetAuthUserDataType = {
    type: typeof SET_USER_DATA,
    payload: SetAuthUserDataPayloadType
}

type DispatchType = Dispatch<ActionTypes>

export const setUserData = (id: number | null, email: string | null, login: string | null, isAuth: boolean): SetAuthUserDataType => ({type: SET_USER_DATA, payload: {id, email, login, isAuth}})

type GetCaptchaUrlSuccessPayloadType = {
    captchaUrl: string
}

type GetCaptchaUrlSuccessType = {
    type: typeof GET_CAPTCHA_URL_SUCCESS,
    payload: GetCaptchaUrlSuccessPayloadType
}

type ThunkType = ThunkAction<Promise<void>, AppStateType, unknown, ActionTypes>

export const getCaptchaUrlSuccess = (captchaUrl: string): GetCaptchaUrlSuccessType => ({type: GET_CAPTCHA_URL_SUCCESS, payload: {captchaUrl}})

export const getData = (): ThunkType => async (dispatch) => {
        let data = await authAPI.me()
            if (data.resultCode === ResultCodesEnum.Success) {
                let {id, email, login} = data.data
                dispatch(setUserData(id, email, login, true))
            }
    }

export const login = (email: string, password: string, rememberMe: boolean, captcha: string): ThunkType => async (dispatch: any) => {
    let data = await authAPI.login(email, password, rememberMe, captcha) 
        if(data.resultCode === ResultCodesEnum.Success) {
            dispatch(getData())
        } else {
            if(data.resultCode === ResultCodeForCaptchEnum.CaptchaIsRequired) {
                dispatch(getCaptchaUrl())
            }
            let message = data.messages.length > 0 ? data.messages[0] : 'Some Error'
            dispatch(stopSubmit('login', {_error: message}))
        }
}

export const getCaptchaUrl = (): ThunkType => async (dispatch) => {
    const data = await securityAPI.getCaptchaUrl() 
    const captchaUrl = data.url
    dispatch(getCaptchaUrlSuccess(captchaUrl))
}

export const logout = (): ThunkType => async (dispatch) => {
    let data = await authAPI.logout()
        if(data.resultCode === ResultCodesEnum.Success) {
            dispatch(setUserData(null, null, null, false))
        }
}


export default authReducer